const styles = {
    container:{
        marginTop: 250,
        textAlign: 'center',

        
    },

    paper: {
        marginTop: 0,
        //display: 'flex',
        //flexDirection: 'column',
        //alignItems: 'center',
    },

};


class Authentication extends Component{

    constructor(props) {
        super(props);
        this.oAuth2TokenGet = this.oAuth2TokenGet.bind(this);

    
        this.state = {
            token: '',
			hubs: [],
            projects: [],
            uEmail: ''
            
        };
	};


    componentDidMount(){
        
    };

    oAuth2TokenGet() {
        // TODO: First try to get the token from sessionStorage here AFYdTXU0lRLRh9kmE85Ofhcr5Txh38oY
      
        //Build the oauth request url
        //const responseType = 'token'; 
        //http://localhost:3000/api/forge/callback/oauth
        //https://dregisterstruxit.herokuapp.com/api/forge/callback/oauth
        const clientId = 'epeFxGSz8mG7DTNk86PBuDVFAx40wbAZ';
        const scope = 'data:read data:write account:read bucket:read';
        const redirectUri = 'https://mjmthimunye.github.io/acc-automation/api/forge/callback/oauth';
        const url = `https://developer.api.autodesk.com/authentication/v1/authorize?response_type=token&client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scope}`;
        
        // Open a new window
        //const win = window.open(url);
		//const win = window.open(url, '_self');
        const win = window.open(url, 'Authenticate', 'height=600,width=450');
        if (win) win.focus();
        
        
        const pollTimer = window.setInterval(() => {
          try {
            if (!!win && win.location.href.indexOf(redirectUri) !== -1) {
              window.clearInterval(pollTimer);
      
              // Get the URL hash with your token in itS
              const hash = win.location.hash;
              win.close();
      
              // Parse the string hash and convert to object of keys and values
              const result = hash.substring(1)
                .split('&')
                .map(i => i.split('='))
                .reduce((prev, curr) => ({
                  ...prev,
                  [curr[0]]: curr[1],
                }), {});

				//console.log(result.access_token)
				this.setState({token:result.access_token});

                fetch('https://developer.api.autodesk.com/userprofile/v1/users/@me', { 
					headers: {
						'Authorization': `Bearer ${result.access_token}`
					}
				})
				.then(res => res.text())
				.then(data => {
					let json = JSON.parse(data)
					let pretty = JSON.stringify(json, null, 2)
					//console.log(json)
					//console.log(pretty);
					//this.setState({ uEmail: json.emailId});
                    this.setState({ userInfo: json});
                    this.setState({ uImage: json.profileImages.sizeX40});

				})

				fetch('https://developer.api.autodesk.com/project/v1/hubs', { 
					headers: {
						'Authorization': `Bearer ${result.access_token}`
					}
				})
				.then(res => res.text())
				.then(data => {
					let json = JSON.parse(data)
					let pretty = JSON.stringify(json, null, 2)
					//console.log(json.data)
					//console.log(pretty);
					this.setState({ hubs: json.data });


					this.props.history.push( {pathname: "/home",
					    state: { hubs: json.data, token: this.state.token , userInfo: this.state.userInfo, uImage: this.state.uImage}});
				
				})

    
				//Calculate when the token expires and store in the result object
				result.expires_at = Date.now() + parseInt(hash.expires_in, 10);
		
				//TODO: Persist result in sessionStorage here
            }
          } catch (err) {
            // do something or nothing if window still not redirected after login
          }
        }, 100);
    };


    render(){

        return(

            <div style={styles.container}>

                <div className={styles.paper}>
                
                    <Button onClick={this.oAuth2TokenGet} style={{marginTop: "3%"}}>
                        <Icon icon="simple-icons:autodesk" style={{fontSize: 60, color: '#000512'}}/>
                    </Button>

                    <h6 style={{marginTop: 15}}>Sign In to ACC</h6>
                
                </div>
               
            </div>       


        )
    }
}


export default Authentication;



