import React, { Component } from 'react';
import { Panel, Placeholder, Stack, Navbar, Loader, Progress, Nav, DOMHelper, Tree, Modal, Button} from 'rsuite';
import { Icon } from '@iconify/react';
import FolderFillIcon from '@rsuite/icons/FolderFill';
import * as XLSX from 'xlsx';
import axios from "axios";
const { getHeight} = DOMHelper;

class FileAttributes extends Component {
    
    constructor(props){
        super(props);

        this.state = {

            filesRenamed: false,
            token: '',
            userInfo: {},
            uImage: '',
            loading: true,
            percent: 0,
            value: [],
            updateSure: false,
            folderUpdate: {}
        };
    };

    componentDidMount(){

        this.setState({token: this.props.location.state.token});
        this.setState({userInfo: this.props.location.state.userInfo});
        this.setState({uImage: this.props.location.state.uImage});

        var aToken = this.props.location.state.token;

        this.getFolders(aToken);

    };

    //Fetch tree folder nodes
    fetchNodes = file => {

        //console.log(file);

        var folderId = file.id;

        return new Promise(resolve => {
            setTimeout(() => resolve(this.getChildren(folderId)), 500);
        });
    };

    //Get tree folder children
    getChildren = async(folderId) => {

        const {token} = this.state;

        var projectId = 'b.ed7b7afd-f59b-4eb7-9666-17b76ab25152';

        let config = {
            method: 'get',
            //hardcoded
            url: `https://developer.api.autodesk.com/data/v1/projects/${projectId}/folders/${folderId}/contents`,
            headers: { 
                'Authorization': `Bearer ${token}`
            }
        };

        axios.interceptors.response.use(response => response, error => {
            const status = error.response ? error.response.status : null
        
            if (status === 429) {
        
                //console.log("Found 429");
                return axios.request(error.config);
            }
        
            return Promise.reject(error);
        });

        //console.log(data);

        var treeData = [];
        
        await axios(config)
        .then((response) => {

            //console.log(response.data);

            var folders = response.data.data;
            //console.log(folders);

            for (let index = 0; index < folders.length; index++) {
                const ele = folders[index];

                treeData.push({
                    "label": ele.attributes.name,
                    "value" : Math.random() * 1e18,
                    "children" : [],
                    "id": ele.id,
                    "text": ele.attributes.name,
                    "type" : ele.type
                })
                
            };

            //console.log(treeData);
  
        })
        .catch(function (error) {
            console.log(error);
        });

        //console.log(treeData);

        return treeData;

    };

    //Get Main folders in ACC project
    getFolders = async(aToken) => {

        //Get first "940_BIM" folder contents
        let config = {
            method: 'get',
            //hardcoded
            url: 'https://developer.api.autodesk.com/data/v1/projects/b.ed7b7afd-f59b-4eb7-9666-17b76ab25152/folders/urn:adsk.wipemea:fs.folder:co.wH2C5EqZTgCQgHr7HXxUDg/contents',
            headers: { 
                'Authorization': `Bearer ${aToken}`
            }
        };

        axios.request(config)
        .then((response) => {
            //console.log(response.data);

            var folders = response.data.data;

            //console.log(folders);

            var treeData = [];

            folders.forEach(ele => {
                treeData.push({
                    "label": ele.attributes.name,
                    "value" : Math.random() * 1e18,
                    "children" : [],
                    "id": ele.id,
                    "text": ele.attributes.name,
                    "type" : ele.type
                })
            });

            //console.log(treeData);

            this.setState({
                loading: false,
                treeData: treeData
            });

       
        })
        .catch((error) => {
            console.log(error);
        });

    };

    //Convert sheet tp array
    sheet2arr = function(sheet){
        var result = [];
        var row;
        var rowNum;
        var colNum;
        var range = XLSX.utils.decode_range(sheet['!ref']);
        for(rowNum = range.s.r; rowNum <= range.e.r; rowNum++){
           row = [];
            for(colNum=range.s.c; colNum<=range.e.c; colNum++){
               var nextCell = sheet[
                  XLSX.utils.encode_cell({r: rowNum, c: colNum})
               ];
               if( typeof nextCell === 'undefined' ){
                  row.push(void 0);
               } else row.push(nextCell.w);
            }
            result.push(row);
        }
        return result;
    };

    //Onchange function to get excel data
    handleUpload = (e) => {

        const {compArray, folderUpdate} = this.state;
        const aToken = this.state.token;

        e.preventDefault();
    
        var files = e.target.files, 
        f = files[0];

        //check if file selected is excel file

        //console.log(f);

        var reader = new FileReader();
        reader.onload = (e) => {
            var data = e.target.result;

            const table = XLSX.read(data, {type: 'binary'});
            const sheet = table.Sheets[table.SheetNames[1]];
            var range = XLSX.utils.decode_range(sheet['!ref']);
            //console.log(range.e.r);

            let readedData = XLSX.read(data, {type: 'binary'});
            //console.log(readedData);
            const wsname = readedData.SheetNames[1];
            //console.log(wsname);

            const ws = readedData.Sheets[wsname];
            //console.log(ws);
    
            /* Convert array to json*/
            const dataParse = XLSX.utils.sheet_to_json(ws, {header:1});
            //console.log(dataParse[0]);

            var excelArray = this.sheet2arr(sheet)
            console.log(excelArray);

            var newDataParse = dataParse.splice(1);
            console.log(newDataParse);
            
            //console.log(folderUpdate);

            this.updateAttributes(folderUpdate);


        };

        reader.readAsBinaryString(f)
    };

    //Increment progress bar
    increase = (item) => {

        const {percent} = this.state;

        const value = Math.min(percent + 100/item, 100);

        this.setState({
            percent: value
        });

    };

    //Navigate to Home
    navigateHome = () => {

        const {token, userInfo, uImage} = this.state;

        this.props.history.push( {pathname: "/home", state: { token: token, userInfo: this.state.userInfo, uImage: this.state.uImage }});

    };

    //Validate if user wants update folder
    updateValidate = (value) => {

        this.setState({
            folderUpdate: value,
            updateSure: true
        });
    };

    //Update File attributes
    updateAttributes = async (folderUpdate) => {

        const {token} = this.state;

        //console.log(folderUpdate);

        var fId = folderUpdate.id;
        var projectId = 'b.ed7b7afd-f59b-4eb7-9666-17b76ab25152';

        //put in settimeout when looping
        var folderContents = await this.getFolderContents(fId, projectId);
        console.log(folderContents);

        const itemsID = [];
        const filesID = [];

        for(var j = 0; j< folderContents.data.length; j++)
        {
            //console.log(res.data.data[j].id);
            itemsID.push(folderContents.data[j].id);
        }

        for(var i = 0; i< folderContents.included.length; i++)
        {
            //console.log(res.data.included[i].id);
            filesID.push(folderContents.included[i].id);
        }

        //console.log(itemsID);
        //console.log(filesID);

        var promises = [];

        for (let index = 0; index < itemsID.length; index++) {
            const item = itemsID[index];
            const file = filesID[index];

            var data = {
                'urns': [
                    file,
                    item
                ]
            };
    
            //console.log(data);
            //console.log(projectIDNew);

            var config = {
                method: 'post',
                url: `https://developer.api.autodesk.com/bim360/docs/v1/projects/b.ed7b7afd-f59b-4eb7-9666-17b76ab25152/versions:batch-get`,
                headers: { 
                    'Authorization': `Bearer ${token}`, 
                    'Content-Type': 'application/json', 
                    
                },
                data : data
            };


            promises.push(axios(config)
            .catch(function (error) {
                console.log(error);
            }));  
  
        }

        await Promise.all(promises).then((response) => {

            console.log(response);

        } )

        //check to see if folder has other folders
        //var index = json.data.findIndex(x => x.type ==="items");
        //console.log(index);
        // let data = folderContents.find(function(ele) {
        //     return ele.type === "folders";
        // });

        // let data2 = folderContents.find(function(ele) {
        //     return ele.type === "items";
        // });

        // console.log(data);
        // console.log(data2);

    
        // setTimeout(async () => {
        //     // var foldersD = await this.getFolderContents(fId, projectId);
        //     // var foldersDFlat = foldersD.flat(Infinity);

        //     console.log(foldersDFlat);

        //     for (let index = 0; index < foldersDFlat.length; index++) {
        //         const folderD = foldersD[index];

        //         var folderIdD = folderD.id;
        //         folderIdsD.push(folderIdD);

        //         //this.renameFolder(folderIdD, projectId, element[4]);
        //     };
        // }, 1000);

        // for (let index = 0; index < folderContents.length; index++) {
        //     const element = folderContents[index];

        //     if(element.type === "items")
        //     {
        //         console.log(element);
        //     }
            
        // }
    };

    //Get folder contents
    getFolderContents = async (folderId, projectId) => {

        const aToken = this.state.token;
        var folders = [];

        let config = {
            method: 'get',
            //hardcoded
            url: `https://developer.api.autodesk.com/data/v1/projects/${projectId}/folders/${folderId}/contents`,
            headers: { 
                'Authorization': `Bearer ${aToken}`
            }
        };

        axios.interceptors.response.use(response => response, error => {
            const status = error.response ? error.response.status : null
        
            if (status === 429) {
        
                //console.log("Found 429");
                return axios.request(error.config);
            }
        
            return Promise.reject(error);
        });

          
        await axios.request(config)
        .then((response) => {

            //console.log(response.data);
            var result = response.data;

            //console.log(result);
            folders.push(result)

        })
        .catch((error) => {
            console.log(error);
        });
        
        return folders[0];

    };

    render(){

        const {filesRenamed, percent, userInfo, treeData, value, loading, updateSure} = this.state;
        const status = percent === 100 ? 'success' : null;
        const color = percent === 100 ? '#52c41a' : '#3385ff';

        return(
            <div className='home-page'>
                
                <Navbar style={{backgroundColor: '#000512'}}>
                    <Navbar.Brand href="#"><h6 style={{color: 'white'}}>AECOM</h6></Navbar.Brand>
                    <Nav pullRight onClick={this.navigateHome}>
                        <Nav.Item icon={<Icon icon="ion:home" color='white'/>} style={{color: 'white'}}>Home</Nav.Item>
                        <Nav.Item icon={<Icon icon="basil:user-solid" color='white' style={{fontSize: 22}}/>} style={{color: 'white'}}>{userInfo.userName}</Nav.Item>        
                    </Nav>
                </Navbar>

                {
                    loading ? 
                    <div style={{ width: 120, marginTop: "5%", marginLeft: "46%" }}>
                        <Loader backdrop content="loading..." vertical />
                    </div> 
                    :
                    <div style={{textAlign: 'center', marginTop: "3%"}}>

                        <h5 style={{marginBottom: 65, marginLeft: 5}}>Update File Custom Attributes</h5>

                        <Panel shaded style={{ display: 'inline-block', cursor: "pointer", width: 600, height: getHeight(window) - 260, paddingTop: "0%"}}>
                            <Tree
                                data={treeData}
                                style={{ width: 565 , maxHeight: getHeight(window) - 300, height: "100%"}}
                                value={value}
                                onSelect = {(value) => 
                                    this.updateValidate(value)
                                }
                                onChange={value => this.setState({
                                    value: value
                                })}
                                getChildren={(value) => this.fetchNodes(value)}
                                renderTreeNode={node => {
                                    return (
                                    <>
                                        {node.children ? <FolderFillIcon color='#000512'/> : <FolderFillIcon color='#000512'/>} {node.label}
                                    </>
                                    );
                                }}
                            />       
                        </Panel>
               
                    </div>
                }

            {/* Modal for update attributes validation */}
            <Modal backdrop={true} keyboard={false} open={updateSure} onClose={() => this.setState({
                updateSure: false
            })} style={{marginTop: "2%"}}>
                <Modal.Header>
                    <Modal.Title>Update ACC Attributes</Modal.Title>
                </Modal.Header>

                <Modal.Body>
                    <h6>Are you sure you want to update attributes for files in this folder?</h6>
                </Modal.Body>
                <Modal.Footer>
                    <label htmlFor='inputId' onChange={(e) => this.handleUpload(e)}>
                        <div style={{display: "flex" ,flexDirection: 'row' , border: '2px solid #003153' , width: 200, 
                                borderRadius: 5,  cursor: "pointer", height: 35,
                                backgroundColor: '#000512',  marginLeft: "65%", marginTop: "10%", padding: 5}}>
                            <input  id="inputId"  type="file" name="file"  hidden/>
                            <Icon icon="ic:round-file-open" style={{marginBottom: 1, color: "white",  fontSize: 18}}/>
                            <h7 style={{marginBottom: 1, fontSize: 14, marginLeft: 8, color: 'white'}}>Select Excel Template File</h7> 
                        </div>
                    </label>

                </Modal.Footer>
            </Modal>
            
            </div>
        )
    }
};

export default FileAttributes;