    //Get folder contents
    getFolderContents = async (folderId, projectId) => {

        const aToken = this.state.token;
        var folders = [];

        let config = {
            method: 'get',
            //hardcoded
            url: `https://developer.api.autodesk.com/data/v1/projects/${projectId}/folders/${folderId}/contents`,
            headers: { 
                'Authorization': `Bearer ${aToken}`
            }
        };

        axios.interceptors.response.use(response => response, error => {
            const status = error.response ? error.response.status : null
        
            if (status === 429) {
        
                //console.log("Found 429");
                
                return axios.request(error.config);

                // setTimeout(
                //     promises.push(axios(config)
                //     .catch(function (error) {
                //         console.log(error);
                //     })) 
                //     , 4000);

                //console.log(error.response);

                //console.log(error.response.headers['x-retry-after']);

                // for (var pair of error.response.headers.entries())
                // {
                //     console.log(pair);
                // }

                //const retryAfter = error.response.headers.get('retry-after')
                //console.log(retryAfter);

            }
        
            return Promise.reject(error);
        });

        // intercept
        // axiosRetry(axios, {
        //     retries: 3,
        //     retryCondition: (error) => {
        //     return error.response.status === 429
        //     },
        // });

          
        await axios.request(config)
        .then((response) => {
            //console.log(response.data.data);

            folders.push(response.data.data);
        })
        .catch((error) => {
            console.log(error);
        });
        
        return folders;

    };

    //Convert sheet tp array
    sheet2arr = function(sheet){
        var result = [];
        var row;
        var rowNum;
        var colNum;
        var range = XLSX.utils.decode_range(sheet['!ref']);
        for(rowNum = range.s.r; rowNum <= range.e.r; rowNum++){
           row = [];
            for(colNum=range.s.c; colNum<=range.e.c; colNum++){
               var nextCell = sheet[
                  XLSX.utils.encode_cell({r: rowNum, c: colNum})
               ];
               if( typeof nextCell === 'undefined' ){
                  row.push(void 0);
               } else row.push(nextCell.w);
            }
            result.push(row);
        }
        return result;
    };

