import React, { Component } from 'react';
import { Panel, Placeholder, Stack, Navbar, Nav } from 'rsuite';
import { Icon } from '@iconify/react';


const styles = {
    container:{
        marginTop: 230,
        textAlign: 'center',
        
    },

};


class Home extends Component {
    
    constructor(props){
        super(props);

        this.state = {

            open: false,
            token: '',
            userInfo: {},
            uImage: ''
        };
    };

    componentDidMount(){

        this.setState({token: this.props.location.state.token});
        this.setState({userInfo: this.props.location.state.userInfo});
        this.setState({uImage: this.props.location.state.uImage});
        //console.log(this.props.location.state);

    };

    //Navigate to FolderPermissions
    navigateFolderPermissions = () => {
        const {token, userInfo, uImage} = this.state;

        this.props.history.push( {pathname: "/fpermissions", state: { token: token, userInfo: this.state.userInfo, uImage: this.state.uImage }});

    };

    //Navigate to FolderNames
    navigateFolderNames = () => {
        const {token, userInfo, uImage} = this.state;

        this.props.history.push( {pathname: "/fnames", state: { token: token, userInfo: this.state.userInfo, uImage: this.state.uImage }});

    };

    navigateUpdateAttributes = () => {

        const {token, userInfo, uImage} = this.state;

        this.props.history.push( {pathname: "/fattributes", state: { token: token, userInfo: this.state.userInfo, uImage: this.state.uImage }});

    };


    render(){

        const {userInfo} = this.state;

        return(

            <div>

                <Navbar style={{backgroundColor: '#000512'}}>
                    <Navbar.Brand href="#"><h6 style={{color: 'white'}}>AECOM</h6></Navbar.Brand>
                    <Nav pullRight >
                        <Nav.Item icon={<Icon icon="basil:user-solid" color='white' style={{fontSize: 22}}/>} style={{color: 'white'}}>{userInfo.userName}</Nav.Item>
                    </Nav>
                </Navbar>

                <div fixed="true" style={styles.container}>

                    <div className={styles.paper}>
                        
                        <Stack spacing={30} style={{paddingLeft: '22%', marginTop: "17%"}}>

                            <Panel onClick={this.navigateFolderNames} header="Rename ACC Folders" shaded style={{ display: 'inline-block', cursor: "pointer", width: 270, height: 120}}>
                                <Icon icon="ic:round-drive-file-rename-outline" style={{fontSize: 40, color: '#000512'}}/>
                            </Panel>

                            <Panel onClick={this.navigateFolderPermissions} header="Change ACC Folder Permissions" shaded style={{ display: 'inline-block', cursor: "pointer", width: 270, height: 120}}>
                                <Icon icon="fluent-mdl2:permissions-solid" style={{fontSize: 27, color: '#000512', marginTop: 8}}/>
                            </Panel>  

                            <Panel onClick={this.navigateUpdateAttributes} header="Update ACC File Attributes" 
                                shaded style={{ display: 'inline-block', cursor: "pointer", width: 270, height: 120}}>
                                <Icon icon="ic:round-edit-attributes"  style={{fontSize: 50, color: '#000512', marginTop: 0}}/>
                            </Panel>

                        </Stack>

                    </div>
                        
                </div>


            </div>
           
        )
    }
};

export default Home;