import React, { Component } from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Home from './pages/Home';
import Authenticate from './pages/Authentication';
import FolderNames from './pages/FolderNames/FolderNames';
import FolderPermissions from './pages/FolderPermissions/FolderPermissions';
import FileAttributes from './pages/FileAttributes/FileAttributes';
import './App.css';
import "rsuite/dist/rsuite.min.css";

class App extends Component {
	render() {
		return (
			<Router>
				<div className="container" >			
					
					<Switch>

						<Route path="/home" component={Home} />	
						<Route path="/fnames" component={FolderNames} />	
						<Route path="/fattributes" component={FileAttributes} />	
						<Route path="/fpermissions" component={FolderPermissions} />					
						<Route path="/" component={Authenticate} />

					</Switch>
				
				</div>
			</Router>
		);
	}
}

export default App;
